import React, { useEffect, useState } from "react";
import SectionHowItWork from "components/SectionHowItWork/SectionHowItWork";
import BackgroundSection from "components/BackgroundSection/BackgroundSection";
import SectionPromo1 from "components/SectionPromo1";
import { Helmet } from "react-helmet";
import SectionHero2 from "components/SectionHero/SectionHero2";
import PageTitle from "shared/meta/PageTitle";
import SectionSliderLargeProduct from "components/SectionSliderLargeProduct";
import SectionSliderProductCard from "components/SectionSliderProductCard";
import DiscoverMoreSlider from "components/DiscoverMoreSlider";
import SectionGridMoreExplore from "components/SectionGridMoreExplore/SectionGridMoreExplore";
import SectionPromo2 from "components/SectionPromo2";
import SectionSliderCategories from "components/SectionSliderCategories/SectionSliderCategories";
import SectionGridFeatureItems from "./SectionGridFeatureItems";
import SectionPromo3 from "components/SectionPromo3";
import SectionClientSay from "components/SectionClientSay/SectionClientSay";
import SectionMagazine5 from "containers/BlogPage/SectionMagazine5";
import Heading from "components/Heading/Heading";
import ButtonSecondary from "shared/Button/ButtonSecondary";
import { PRODUCTS, SPORT_PRODUCTS } from "data/data";
import { useDispatch } from "react-redux";
import { useAppSelector, useAppDispatch } from '../../app/hooks';
import { BASEURL } from "../../app/baseUrl";
import { getConfig } from "app/shop/storeAction";

import { getStoreConfig } from "app/shop/storeAction";
function PageHome() {
  const dispatch = useAppDispatch();

  const [storeName, setStoreName] = useState('');
  const [col1, setCol1] = useState([]);
  const [col2, setCol2] = useState([]);
  const PAll = async () => {
    const request = await fetch(`${BASEURL}/main`);
    const res = await request.json();
    if (res) {
      setCol1(res.col_1_products);
      setCol2(res.col_2_products);
    }

  };



  useEffect(() => {
    dispatch(getConfig()).then((res) => {
      setStoreName(res.store.shop_name);
    });
    PAll();
  }, []);

  return (
    <div className="relative overflow-hidden nc-PageHome">
      <Helmet>
        <title>Get Huge Discounts | Online Shopping | {storeName} </title>
      </Helmet>

      {/* SECTION HERO */}
      <SectionHero2 />

      {/* <div className="mt-24 lg:mt-32">
        <DiscoverMoreSlider />
      </div> */}

      <div className="container relative my-24 space-y-24 lg:space-y-32 lg:my-32">
        {/* SECTION */}
        {col1.length > 0 && (
          <SectionSliderProductCard
            data={col1}
          />
        )}


        <div className="py-24 border-t border-b lg:py-32 border-slate-200 dark:border-slate-700">
          <SectionHowItWork />
        </div>

        {/* SECTION */}
        {/* <SectionPromo1 /> */}

        {/* SECTION */}
        {/* <div className="relative py-24 lg:py-32">
          <BackgroundSection />
          <SectionGridMoreExplore />
        </div>

        <SectionSliderProductCard
          heading="Best Sellers"
          subHeading="Best selling of the month"
        /> */}

        {/*  */}
        {/* <SectionPromo2 /> */}

        {/* SECTION 3 */}
        {/* <SectionSliderLargeProduct cardStyle="style2" /> */}

        {/*  */}
        {/* <SectionSliderCategories /> */}

        {/* SECTION */}
        {/* <SectionPromo3 /> */}

        {/* SECTION */}
        <SectionGridFeatureItems data={col2} />

        {/* <div className="relative py-24 lg:py-32">
          <BackgroundSection />
          <div>
            <Heading rightDescText="From the Ciseco blog">
              The latest news
            </Heading>
            <SectionMagazine5 />
            <div className="flex justify-center mt-16">
              <ButtonSecondary>Show all blog articles</ButtonSecondary>
            </div>
          </div>
        </div> */}

        {/*  */}
        {/* <SectionClientSay /> */}
      </div>
    </div>
  );
}

export default PageHome;
