import {
  CONFIG,
  CONFIG_ERROR,
  CONFIG_PENDING,
  } from "./storeActionTypes";
  import { RootState } from "app/store";

  const initialState = {
    storeConfig: null,
    isLoading: false,
    isError: false,
    error: null,
  };
  const configReducer = (state = initialState, action: { type: any; error: any; payload: any; }) => {
    switch (action.type) {
      case CONFIG_PENDING:
        return {
          ...state,
          storeConfig: null,
          isLoading: true,
        };
      case CONFIG_ERROR:
        return {
          ...state,
          isLoading: false,
          isError: true,
          error: action.error,
        };
      case CONFIG:
        return {
          ...state,
          storeConfig: action.payload,
          isLoading: false,
          isError: false,
          error: null,
        };
      default:
        return state;
    }
  };

  export const storeConfig = (state: RootState) =>
  state.storeConfig;
  export default configReducer;
  