import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import store from '../../app/shop/storeReducers';
import { useAppSelector, useAppDispatch } from '../../app/hooks';
import { getConfig } from "app/shop/storeAction";

function Logo() {
  const dispatch = useAppDispatch();

  const [storeName, setStoreName] = useState('');
  
  useEffect(() => {
    dispatch(getConfig()).then((res) => {
      setStoreName(res.store.shop_name);
    });
  
   }, []);
  return (
    <Link
      to="/"
      className={`ttnc-logo inline-block font-medium text-[30px] text-black flex-shrink-0 capitalize`}
    >
      {/* THIS USE FOR MY CLIENT */}
      {/* PLEASE UN COMMENT BELLOW CODE AND USE IT */}
   {storeName}
    </Link>
  );
}


export default Logo;
